import { Component } from 'react';

export default class Display extends Component {
    constructor() {
        super();
    }

    render() {
        if (!this.props.item) return <div></div>;
        const {item} = this.props;
        return (
            <div style={{
                position: 'fixed',
                right: '0',
                top: '0',
                width: '400px',
                height: '100%',
                padding: '20px'
            }}>
                {item.name}
            </div>
        );
    }
}
